@import '../../shared/styles/variables';

.inner_wrapper {
  padding-top: 100px;
  padding-bottom: 100px;
  @media screen and (max-width: 767px) {
	padding-bottom: 40px;
  }

}

.title {
	text-align: center;
  text-transform: uppercase;
  font-size: 48px;
  margin-bottom: 60px;
}

.items {
	display: flex;
	justify-content: center;
	padding: 0 30px;
	@media screen and (max-width: 767px) {
		flex-direction: column;
		align-items: center;
	  }

	  .item{
		margin-right: 150px;
		min-width: 250px;

		&:last-child{
			margin-right: 0;
		}
		@media screen and (max-width: 767px) {
			text-align: center;
			margin: 0;
			margin-bottom: 20px;
		  }
	  }

	.subtitle{
		font-weight: 400;
		line-height: 1.5;
		letter-spacing: 0.00938em;
		margin-bottom: 1rem;
		font-size: 1.1rem;
	}

	.contactList{
		@media screen and (max-width: 767px) {
			display: flex;
			flex-direction: column;
			align-items: center;
		  }
	
		.contactItem{
			
			font-size: 14px;
			color: #ffffff8a;
			margin-bottom: 10px;

			a{
				display: flex;
				align-items: center;
				color: inherit;
				text-decoration: none;
				transition: all $transition;

				@media screen and (max-width: 767px) {
					text-align: center;
				  }

				.icon{
					margin-right: 10px;
				}
			}

			&:hover{
				a{
					color: $primary-text-color;
				}
			}
		}

	}
}


.btn {
  padding: 13px 28px;
  border: none;
  border-radius: 60px;

  color: var(--main-bg);
  background-color: var(--text-color);
}

.modal_hidden {
  transform: scale(0.5);
  opacity: 0;
  transition: all 0.4s linear;
}

.modal_show {
  transform: scale(1);
}

.socialList {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  padding-top: 100px;

  .socialIcon {
    cursor: pointer;
    width: 50px;
    margin-left: 30px;

    transition: fill 150ms linear;

    fill: $primary-text-color;

    &:first-child {
      margin: 0;
    }

    &:hover {
      fill: rgb(255, 174, 0);
    }

    @media screen and (max-width: 767px) {
      width: 40px;
    }
  }
}
